/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.boton-amarillo {
  background-color: #e08709;
  color: #ffffff;
  text-decoration: none;
  font-weight: 700;
  padding: 1rem 3rem;
  text-align: center;
  display: block;
  margin: 1.5rem 0;
  border: none;
}
.boton-amarillo:hover {
  background-color: #c77808;
  cursor: pointer;
}

.boton-amarillo-inline {
  background-color: #e08709;
  color: #ffffff;
  text-decoration: none;
  font-weight: 700;
  padding: 1rem 3rem;
  text-align: center;
  display: inline-block;
  margin: 1.5rem 0;
  border: none;
}
.boton-amarillo-inline:hover {
  background-color: #c77808;
  cursor: pointer;
}

.boton-verde {
  background-color: #71b100;
  color: #ffffff;
  text-decoration: none;
  font-weight: 700;
  padding: 1rem 3rem;
  text-align: center;
  display: inline-block;
  margin: 1.5rem 0;
  border: none;
}
.boton-verde:hover {
  background-color: #619800;
  cursor: pointer;
}

.dark-mode {
  transition: all 0.2s linear;
  background-color: #1a1a1a;
}
.dark-mode p {
  color: #ffffff;
}
.dark-mode a,
.dark-mode h1,
.dark-mode h2,
.dark-mode h3,
.dark-mode h4 {
  color: #e1e1e1;
}
.dark-mode .anuncio {
  background-color: #333333;
  border-color: gainsboro;
}
.dark-mode .anuncio .precio {
  color: #71b100;
}
.dark-mode .anuncio .icono {
  filter: invert(100%);
}
.dark-mode .texto-informacion blockquote {
  color: #ffffff;
}
.dark-mode .resumen-propiedad .precio {
  color: #71b100;
}
.dark-mode .resumen-propiedad .icono {
  filter: invert(100%);
}
.dark-mode .formulario label,
.dark-mode .formulario legend,
.dark-mode .formulario p {
  color: #e1e1e1;
}
.dark-mode .formulario input:not([type=submit]),
.dark-mode .formulario textarea,
.dark-mode .formulario select {
  background-color: #333333;
  color: #ffffff;
  border-color: #6e6e6e;
}

.no-webp .inicio {
  background-image: url(../img/header.jpg);
}

.webp .inicio {
  background-image: url(../img/header.webp);
}

.header {
  background-color: #333333;
  padding: 1rem 0 3rem 0;
}
.header.inicio {
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 70rem;
  }
}
.header.inicio .contenido-header {
  gap: 20rem;
}

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.contenido-header h1 {
  font-size: 3rem;
  color: #ffffff;
  font-weight: 400;
}
.contenido-header h1 span {
  color: #35acfe;
}
@media (min-width: 768px) {
  .contenido-header h1 {
    font-size: 3.8rem;
  }
}

.derecha {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 3rem;
}
@media (min-width: 768px) {
  .derecha {
    flex-direction: row;
    width: auto;
  }
}
.derecha .dark-mode-boton {
  width: 3rem;
  margin-top: 1rem;
  filter: invert(100%);
}
@media (min-width: 768px) {
  .derecha .dark-mode-boton {
    margin: 0;
  }
}
.derecha .dark-mode-boton:hover {
  cursor: pointer;
}

.barra {
  display: flex;
  flex-direction: column;
  padding-top: 2.5rem;
  align-items: center;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
  }
}

.navegacion {
  visibility: hidden;
  font-size: 1.8rem;
  width: 50%;
  opacity: 0;
  transition: visibility 0.2s ease-in-out, opacity 0.2s linear, height 0.2s linear;
  height: 0;
}
.navegacion a {
  color: #ffffff;
  text-decoration: none;
  display: block;
  text-align: center;
  width: 100%;
}
.navegacion a:hover {
  background-color: rgba(251, 250, 250, 0.212);
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    width: auto;
    align-items: center;
    flex-direction: row;
    gap: 2rem;
    opacity: 1;
    visibility: visible;
  }
}

/* Menu mobile */
.mobile-menu {
  width: 7rem;
  padding: 1.5rem 1.5rem;
  transition: 0.1s linear;
}
@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu:hover {
  background-color: rgba(251, 250, 250, 0.212);
}

.mostrar {
  visibility: visible;
  opacity: 1;
  height: auto;
}

@media (min-width: 768px) {
  .iconos-nosotros {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.icono {
  text-align: center;
}
.icono img {
  height: 10rem;
}
.icono h3 {
  text-transform: uppercase;
}

.footer {
  background-color: #333333;
  padding: 3rem 0 2rem 0;
  margin-bottom: 0;
}
.footer .navegacion {
  visibility: visible;
  opacity: 1;
  justify-content: center;
  margin: 0 auto;
  height: auto;
}
.footer .copyright {
  color: #ffffff;
  text-align: center;
}

@media (min-width: 768px) {
  .contenedor-anuncios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.anuncio {
  background-color: #f0f0f0;
  border: 2px solid #b3b3b3;
  margin-bottom: 2rem;
}

.contenido-anuncio {
  padding: 0 2rem;
}
.contenido-anuncio h3 {
  margin: 1rem 0;
}
.contenido-anuncio p {
  margin: 0;
}

.precio {
  color: #71b100;
  font-weight: 700;
}

.iconos-caracteristicas {
  margin: 2rem 0;
  list-style: none;
  padding-left: 0;
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(3, 1fr);
}
.iconos-caracteristicas li {
  display: flex;
  justify-content: center;
  align-items: center;
}
.iconos-caracteristicas img {
  height: 3rem;
  width: 70%;
}

.no-webp .imagen-contacto {
  background-image: url(../img/encuentra.jpg);
}

.webp .imagen-contacto {
  background-image: url(../img/encuentra.webp);
}

.imagen-contacto {
  background-position: center center;
  background-size: cover;
  background-repeat: none;
  padding: 5rem 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.imagen-contacto h2 {
  color: #ffffff;
  font-weight: 700;
}
.imagen-contacto p {
  color: #ffffff;
}
@media (max-width: 768px) {
  .imagen-contacto p {
    width: 90%;
  }
}

@media (min-width: 768px) {
  .seccion-inferior {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 2rem;
  }
}
.entrada-blog {
  margin-bottom: 3rem;
}
@media (min-width: 768px) {
  .entrada-blog {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2rem;
  }
}
.entrada-blog:last-of-type {
  margin-bottom: 0;
}
.entrada-blog .imagen {
  display: flex;
  justify-content: center;
  align-items: center;
}

.informacion-meta span {
  color: #e08709;
}

.texto-entrada a {
  display: block;
  margin-top: 2rem;
  color: #000000;
}
@media (min-width: 768px) {
  .texto-entrada a {
    margin-top: 0;
  }
}
.texto-entrada h4 {
  margin: 0;
  font-size: 2.2rem;
}
.texto-entrada h4::after {
  content: "";
  display: block;
  width: 15rem;
  height: 0.4rem;
  background-color: #71b100;
  border-radius: 5px;
}
.texto-entrada p {
  font-size: 2rem;
}

.testimonial {
  background-color: #71b100;
  font-size: 2rem;
  padding: 3rem 0;
  color: #ffffff;
  text-align: center;
  border-radius: 3px;
}
.testimonial blockquote::before {
  content: "";
  background-image: url(../img/comilla.svg);
  background-repeat: no-repeat;
  width: 2.5rem;
  height: 3rem;
  color: #ffffff;
  display: inline-block;
}
.testimonial p {
  text-align: right;
  margin: 0 4rem;
  font-style: italic;
}

.formulario p {
  font-size: 1.5rem;
  color: #333333;
  margin: 2rem 0 0.5rem 0;
}
.formulario legend {
  font-size: 2rem;
  color: #333333;
  font-weight: 700;
}
.formulario label {
  display: block;
  font-weight: 700;
  text-transform: uppercase;
}
.formulario input:not([type=submit]),
.formulario textarea,
.formulario select {
  padding: 0.4rem 1rem;
  display: block;
  width: 100%;
  border: 1px solid #bbbbbb;
  background-color: #ffffff;
  margin-bottom: 2rem;
}
.formulario textarea {
  height: 20rem;
  resize: none;
}

.forma-contacto {
  display: flex;
  max-width: 26rem;
}
.forma-contacto input[type=radio] {
  margin: auto 0;
}

@media (min-width: 768px) {
  .informacion {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.texto-informacion blockquote {
  font-size: 2rem;
  font-weight: 700;
  margin: 2rem 3rem 0 3rem;
}
.texto-informacion blockquote span {
  color: #35acfe;
}

.alerta {
  padding: 0.5rem;
  text-align: center;
  color: #ffffff;
  font-weight: 700;
  text-transform: uppercase;
  margin: 1rem 0;
  border-radius: 0.3rem;
}
.alerta.error {
  background-color: #ca2626;
}